const BRANCH = "alert";

class Alert{
    constructor( dispatch ){
        this.dispatch = dispatch
    }

    success = ( lb="", msg="" ) => {
        let message = msg;
        let label = lb;
        if( !label ){
            label = "Thnh cng!"
        }
        this.dispatch({
            branch: BRANCH,
            type: "alertFire",
            payload: {
                type: "success",
                label,
                message
            }
        })
    }

    warning = ( lb="", msg="" ) => {
        let message = msg;
        let label = lb;
        if( !label ){
            label = "Cnh bo!"
        }
        this.dispatch({
            branch: BRANCH,
            type: "alertFire",
            payload: {
                type: "warning",
                label,
                message
            }
        })
    }

    failure = ( lb="", msg="" ) => {
        let message = msg;
        let label = lb;
        if( !label ){
            label = "Li!"
        }
                
        this.dispatch({
            branch: BRANCH,
            type: "alertFire",
            payload: {
                type: "failure",
                label,
                message
            }
        })
    }

    closeAlert = () => {
        this.dispatch({
            branch: BRANCH,
            type: "alertOut",
        })
    }

}

export default Alert
