const dateGenerator = ( dateString ) => {
    const date = new Date( dateString );
    return `${ formatDateNumber(date.getDate()) }/${ formatDateNumber(date.getMonth() + 1) }/${ date.getFullYear() } lc ${ formatDateNumber(date.getHours()) }:${ formatDateNumber(date.getMinutes()) }`
}

const formatDateNumber = (int) => {
    if( int < 10 ){
        return `0${int}`
    }else{
        return `${int}`
    }
}

// const openTab = (url) => {
//     window.open(url, '_blank').focus();
// }
const openTab = (url) => {
    window.location= url;
}



const autoLabel = ( key ) => {
    switch (key) {
        case "INITIALIZING":
            return "Khi ng";
            break;
        case "STARTED":
            return "Bt u";
            break;
        case "PROGRESS":
            return "Thc hin";
            break;
        case "RELEASE":
            return "Trin khai";
            break;
        case "FINAL":
            return "Cui cng";
            break;
        case "COMPLETED":
            return "Hon thnh";
            break;
        case "BUG":
            return "Li";
            break;
        case "SUSPEND":
            return "Tm dng";
            break;
        default:
            return "Khc";
            break;
    }
}

function titleCase(str) {
    return str.toLowerCase().split(' ').map(function(word) {
        return word.replace(word[0], word[0].toUpperCase());
    }).join(' ');
}

const auto_id = () => {
    return 'id' + (new Date()).getTime();
}

const removeDuplicate = ( data ) => {

    const uniqueArray = data.filter((value, index) => {
        const _value = JSON.stringify(value);
        return index === data.findIndex(obj => {
            return JSON.stringify(obj) === _value;
        });
    });
    return uniqueArray
}

export default {
    dateGenerator, openTab, titleCase, autoLabel, auto_id, removeDuplicate
}
