import { useState, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';

export default (props) => {
    const { userAdd, setUserAdd, users, setUsers } = props;
    const [right, setRight] = useState(-500);
    const [height, setHeight] = useState(0);
    const [error, setError] = useState({});

    const validateField = (field, value, updateError = false) => {
        let errorMessage = "";
        switch (field) {
            case "account_string":
                if (!value || value.length < 3 || value.length > 20) {
                    errorMessage = "Tn ng nhp t 3-20 k t";
                }
                break;
            case "pwd_string":
                if (!value || value.length < 8) {
                    errorMessage = "Mt khu phi c t nht 8 k t";
                }
                break;
            case "account_role_label":
                if (!value) {
                    errorMessage = "Vui lng chn loi ti khon";
                }
                break;
            case "fullname":
                if (!value || value.length < 1) {
                    errorMessage = "Vui lng nhp tn ngi dng";
                }
                break;
            case "email":
                const emailRegex = /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i;
                if (!value || !emailRegex.test(value)) {
                    errorMessage = "Vui lng nhp a ch email hp l";
                }
                break;
            case "phone":
                const phoneRegex = /^(\+\d{1,2})?\d{10}$/;
                if (!value || !phoneRegex.test(value)) {
                    errorMessage = "Vui lng nhp s in thoi hp l";
                }
                break;
            case "address":
                if (!value || value.length < 1) {
                    errorMessage = "Vui lng nhp a ch";
                }
                break;
            default:
                break;
        }
        if (updateError) {
            setError((prevError) => ({ ...prevError, [field]: errorMessage }));
        }
        return errorMessage;
        setError({ ...error, [field]: errorMessage });
    };


    const roles = [
        { id: 0, label: "Qun tr vin ( Admin )", value: "admin" },
        { id: 1, label: "Ngi dng ( User ) ", value: "user" },
    ]
    const [errors, setErrors] = useState({
        account_string: "",
        pwd_string: "",
        fullname: "",
        email: "",
        phone: "",
        address: ""
    });
    const handleInputChange = (e, field) => {
        const value = e.target.value;
        let errorMessage = "";

        switch (field) {
            case "account_string":
                errorMessage = value.length < 1 ? "Vui lng nhp tn" : "";
                break;
            // Add more case statements for other fields
        }
        setUser({ ...user, [field]: value });
        setErrors({ ...errors, [field]: errorMessage });
    };
    const { navState, Alert } = useSelector(state => state);
    const dispatch = useDispatch();
    const al = new Alert(dispatch);
    useEffect(() => {
    }, [])
    useEffect(() => {

        if (userAdd) {
            setRight(0)
            dispatch({
                type: "setNavState",
                payload: {
                    navState: false
                }
            })
        } else {
            setRight(-500)
            dispatch({
                type: "setNavState",
                payload: {
                    navState: true
                }
            })
        }
    }, [userAdd])

    const { unique_string, proxy, defaultImage } = useSelector(state => state);
    const [user, setUser] = useState({})

    const blurTrigger = (e) => {
        e.preventDefault();
        setTimeout(() => {
            setHeight(0)
        }, 135)
    }

    const focusTrigger = () => {
        setHeight(100);
    }

    const isFormValid = (user, error) => {
        let valid = true;

        // Danh sch cc trng cn kim tra
        const fieldsToCheck = [
            "account_string",
            "pwd_string",
            "fullname",
            "email",
            "phone",
            "address"
        ];

        // Kim tra tng trng nhp liu
        for (const field of fieldsToCheck) {
            if (error[field]) {
                valid = false;
                break;
            }
        }

        return valid;
    };
    const submit = () => {
        // Kim tra v cp nht li cho tng trng
        const fieldsToCheck = [
            "account_string",
            "pwd_string",
            "fullname",
            "email",
            "phone",
            "address"
        ];
        fieldsToCheck.forEach((field) => validateField(field, user[field]));
        const newError = { ...error };
        fieldsToCheck.forEach((field) => {
            const errorMessage = validateField(field, user[field]);
            newError[field] = errorMessage;
        });
        setError(newError);
        if (isFormValid(user, newError)) { // Kim tra tnh hp l ca d liu
            fetch(`${proxy()}/${unique_string}/create_user`, {
                method: "POST",
                headers: {
                    "content-type": "application/json",
                },
                body: JSON.stringify({ user })
            }).then(res => {
                if (!res.ok) {
                    throw new Error('Thm ngi dng tht bi');
                }
                return res.json();
            })
                .then(data => {
                    const { credential_string } = data;

                    al.success("Thnh Cng", "Thm ngi dng thnh cng")
                    setTimeout(() => {
                        window.location.reload();
                    }, 1600);
                    setUsers({ ...user, credential_string, avatar: defaultImage })
                })
                .catch(error => {
                    // Show an error message when adding the user fails
                    al.failure("Tht bi", "Vui lng kim tra li thng tin");
                });
        } else {
            // Show an error message when the form is not valid
            al.failure("Tht bi", "Vui lng kim tra li thng tin v nhp chnh xc");
        }
    };


    // const submit = () => {
    //     if (user.pwd_string && user.account_string && user.account_role) {
    //         fetch(`${proxy}/${unique_string}/create_user`, {
    //             method: "POST",
    //             headers: {
    //                 "content-type": "application/json",
    //             },
    //             body: JSON.stringify({ user })
    //         }).then(res => {
    //             if (!res.ok) {
    //                 throw new Error('Thm ngi dng tht bi');
    //             }
    //             return res.json();
    //         })
    //             .then(data => {
    //                 const { credential_string } = data;

    //                 al.success("Thnh Cng", "Thm ngi dng thnh cng")
    //                 setTimeout(() => {
    //                     window.location.reload();
    //                 }, 1600);
    //                 setUsers({ ...user, credential_string, avatar: defaultImage })
    //             })
    //             .catch(error => {
    //                 // Show an error message when adding the user fails
    //                 al.failure("Tht bi", "Vui lng kim tra li thng tin");
    //             });
    //     }
    // }

    return (
        <div className={`fixed adduser overflow z-index-1 bg-white r-0 t-0 shadow`} style={{ right: `${right}px` }}>
            <div className="p-t-5 ">
                { /* TOGGLE */}
                <div className="flex">
                    {/* <div className="ml-auto">
                        <span className="text-20-px block p-1">TO TI KHON</span>
                    </div> */}
                    <div className="flex flex-middle w-42-px pointer " onClick={() => { setUserAdd(false) }}>
                        <img src="/assets/icon/right-arrow.png" className="block w-80-pct" />
                    </div>
                </div>
                {/* FORM BODY */}
                <span className="text-20-px block p-1 text-right">Thng tin ti khon</span>
                <div className="form-field w-100-pct flex flex-no-wrap p-0-5 mg-auto">
                    <label className="block w-40-pct">Tn ng nhp</label>

                    {/* <input value={user.account_string} placeholder='Nhp ti khon' onInput={
                        (e) => { setUser({ ...user, account_string: e.target.value }) }
                    } onBlur={(e) => validateField("account_string", e.target.value, true)} className="block w-60-pct border-1  border-radius-8-px p-0-5" />
                     */}
                    <input
                        value={user.account_string}
                        placeholder="Nhp ti khon"
                        onInput={(e) => {
                            setUser({ ...user, account_string: e.target.value });
                            validateField("account_string", e.target.value, true);
                        }}
                        className="block w-60-pct border-1  border-radius-8-px p-0-5"
                    />



                    {/* <input value={user.account_string} type="text" maxLength={6} minLength={1} placeholder="Nhp ti khon" onChange={
                        (e) => { setUser({ ...user, account_string: e.target.value }) }
                    } className="block w-60-pct border-1  border-radius-8-px p-0-5" /> */}
                    {/* <input
                        value={user.account_string}
                        onChange={(e) => handleInputChange(e, "account_string")}
                        className="block w-60-pct border-1  border-radius-8-px p-0-5"
                    /> */}

                </div>

                <div className="form-field w-100-pct flex flex-no-wrap p-l-0-5">
                    <label className="block w-40-pct"></label>
                    <span className="text-red-500 block w-60-pct ">
                        <div className="rel p-b-0-5">
                            <div className="abs">

                                <span className="block text-red text-12-px">
                                    {error.account_string}
                                </span>
                            </div>

                        </div>
                    </span>
                </div>
                <div className="form-field w-100-pct flex flex-no-wrap p-1 mg-auto">
                    <label className="block w-40-pct">Mt khu</label>
                    <input value={user.pwd_string} minLength={2} type="password" placeholder='Nhp mt khu'
                        onChange={(e) => { setUser({ ...user, pwd_string: e.target.value }) 
                        validateField("pwd_string", e.target.value, true)} }className="block w-60-pct border-1  border-radius-8-px p-0-5" />
                </div>
                <div className="form-field w-100-pct flex flex-no-wrap p-l-0-5">
                    <label className="block w-40-pct"></label>
                    <span className="text-red-500 block w-60-pct ">
                        <div className="rel p-b-0-5">
                            <div className="abs">

                                <span className="block text-red text-12-px">
                                    {error.pwd_string}
                                </span>
                            </div>

                        </div>
                    </span>
                </div>
                <div className="form-field w-100-pct flex flex-no-wrap p-1 mg-auto">
                    <label className="block w-40-pct h-20-pct">Loi ti khon</label>
                    <input placeholder='Chn quyn' defaultValue={user.account_role_label ? user.account_role_label : ""} onFocus={focusTrigger} onBlur={blurTrigger} className="block w-60-pct border-1  border-radius-8-px p-0-5" readOnly />

                </div>
                <div className="form-field w-100-pct flex flex-no-wrap p-l-0-5">
                    <label className="block w-40-pct"></label>
                    <span className="text-red-500 block w-60-pct ">
                        <div className="rel p-b-0-5">
                            <div className="abs">

                                <span className="block text-red text-12-px">
                                    {error.account_role}
                                </span>
                            </div>

                        </div>
                    </span>
                </div>
                <div className="form-field w-100-pct flex flex-no-wrap flex-end mg-auto">
                    <div className="w-60-pct">
                        <div className="rel">
                            <div className="abs-default w-100-pct no-overflow bg-white shadow" style={{ height: `${height}px` }}>
                                <div className="block w-100-pct p-0-5 overflow" style={{ height: `${100}px` }}>
                                    {roles.map(role =>
                                        <div key={role.id}>
                                            <span className="block p-0-5 bg-white pointer hover" onClick={() => {
                                                setUser({ ...user, account_role: role.value, account_role_label: role.label });
                                                validateField("account_role_label", role.label); // Kim tra tnh hp l khi chn quyn
                                            }}>{role.label}</span>
                                        </div>
                                    )}
                                </div>
                            </div>
                        </div>
                    </div>
                </div>



                {/* DROPBOX HERE */}




                <span className="text-20-px block p-1 text-right">Thng tin ngi dng</span>
                <div className="form-field w-100-pct flex flex-no-wrap p-0-5 mg-auto">
                    <label className="block w-40-pct">H tn</label>
                    <input value={user.fullname} placeholder='Nhp y  h tn' onChange={
                        (e) => { setUser({ ...user, fullname: e.target.value }) 
                    validateField("fullname", e.target.value, true)} }className="block w-60-pct border-1  border-radius-8-px p-0-5" />
                </div>
                <div className="form-field w-100-pct flex flex-no-wrap p-l-0-5">
                    <label className="block w-40-pct"></label>
                    <span className="text-red-500 block w-60-pct ">
                        <div className="rel p-b-1">
                            <div className="abs">

                                <span className="block text-red text-12-px">
                                    {error.fullname}
                                </span>
                            </div>

                        </div>
                    </span>
                </div>
                <div className="form-field w-100-pct flex flex-no-wrap p-0-5 mg-auto">
                    <label className="block w-40-pct">Email</label>
                    <input value={user.email} type="email" placeholder='Nhp ti khon Email' onChange={
                        (e) => { setUser({ ...user, email: e.target.value }) 
                    validateField("email", e.target.value, true)}} className="block w-60-pct border-1  border-radius-8-px p-0-5" />
                </div>
                <div className="form-field w-100-pct flex flex-no-wrap p-l-0-5">
                    <label className="block w-40-pct"></label>
                    <span className="text-red-500 block w-60-pct ">
                        <div className="rel p-b-1">
                            <div className="abs">

                                <span className="block text-red text-12-px">
                                    {error.email}
                                </span>
                            </div>

                        </div>
                    </span>
                </div>
                <div className="form-field w-100-pct flex flex-no-wrap p-0-5 mg-auto">
                    <label className="block w-40-pct">S di ng</label>
                    <input value={user.phone} maxLength={10} placeholder='Nhp s in thoi' onChange={
                        (e) => { setUser({ ...user, phone: e.target.value }) 
                  validateField("phone", e.target.value, true)}} className="block w-60-pct border-1  border-radius-8-px p-0-5" type="number" />
                </div>
                <div className="form-field w-100-pct flex flex-no-wrap p-l-0-5">
                    <label className="block w-40-pct"></label>
                    <span className="text-red-500 block w-60-pct ">
                        <div className="rel p-b-1">
                            <div className="abs">

                                <span className="block text-red text-12-px">
                                    {error.phone}
                                </span>
                            </div>

                        </div>
                    </span>
                </div>
                <div className="form-field w-100-pct flex flex-no-wrap p-0-5 mg-auto">
                    <label className="block w-40-pct">a ch</label>
                    <input value={user.address} placeholder='Nhp a ch' onChange={
                        (e) => { setUser({ ...user, address: e.target.value }) 
                     validateField("address", e.target.value, true)}} className="block w-60-pct border-1  border-radius-8-px p-0-5" />
                </div>
                <div className="form-field w-100-pct flex flex-no-wrap p-l-0-5">
                    <label className="block w-40-pct"></label>
                    <span className="text-red-500 block w-60-pct ">
                        <div className="rel p-b-1">
                            <div className="abs">

                                <span className="block text-red text-12-px">
                                    {error.address}
                                </span>
                            </div>

                        </div>
                    </span>
                </div>

                <div className="form-field w-100-pct flex flex-no-wrap p-0-5 mg-auto">
                    <button onClick={submit} className="upper pointer block ml-auto border-radius-8-px w-max-content white text-center p-t-0-5 p-b-0-5 p-l-1 p-r-1 no-border bg-green">THM NGAY</button>
                </div>
            </div>
        </div>
    )
}
